.page {
    height: 100vh;
    display: flex;
    flex-direction: column;
    background: #BED7E4;
    >main {
      flex-grow: 1;
      min-width: 1024px;
      max-width: 1440px;
      margin-top: 16px;
      margin-bottom: 16px;
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-self: center;
    }
  }
  #resumeEditor {
      min-width: 35%;
      background: #444;
    }
  #resumePreview {
    width: 61.66667%;
    background: #777;
    flex-grow: 1;
    margin-left: 16px;
  }
  svg.icon {
    height: 1em;
    width: 1em;
    fill: currentColor;
    vertical-align: -0.1em;
    font-size: 16px;
  }


